.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.box {
  height: 200px;
  width: 200px;
  background: pink;
  overflow: hidden;
  font-size: 200px;
  font-family: serif;
  font-weight: bolder;
  line-height: 200px;
  margin-top: -5px;
  margin-bottom: -5px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo,
  .box {
    animation: App-logo-spin infinite 5s linear;
  }
}

/* @media (prefers-reduced-motion: no-preference) {
  .box {
    animation: App-logo-spin infinite 20s linear;
  }
} */

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(-30deg);
  }
  to {
    transform: rotate(330deg);
  }
}
